@charset "UTF-8";
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

.body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: "Roboto", "Arial", sans-serif;
  color: #FAFAFA; }

.body--dark {
  position: relative; }
  .body--dark::after {
    content: '';
    display: inline-block;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(19, 20, 23, 0.5); }

ul, ol, p, button {
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

a {
  color: inherit;
  text-decoration: none; }

* {
  box-sizing: border-box; }

.main {
  width: 100%;
  min-height: 940px;
  background-color: #F2994A;
  background-image: url("../img/alpaca.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .main {
      background-image: url("../img/alpaca@2x.jpg"); } }

.visually-hidden {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.wrapper {
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  max-width: 1240px; }

.menu {
  width: 100%;
  max-width: 375px;
  transition: all 0.6s;
  min-height: 940px;
  background-color: #131417; }
  .menu__submenu, .menu__submenu-menu {
    display: none; }
    .menu__submenu:hover, .menu__submenu:focus, .menu__submenu-menu:hover, .menu__submenu-menu:focus {
      display: block; }
  .menu__item {
    background-color: #252830; }
    .menu__item--red {
      color: #EB5757; }
  .menu__link {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 20px;
    border-top: 1px solid #131417;
    position: relative;
    cursor: pointer;
    transition: all 0.2s; }
    .menu__link:active, .menu__link:focus {
      background-color: #131417; }
    .menu__link span {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap; }
    .menu__link--list {
      /*

            Стили для открытия пунктов подменю без использования js конкретно на планшете и меньше, 
            но работает не идеально потому принято решения для этих разрешений сделать открытие подменю
            с помощью js

            &:active,
            &:focus {
                &::after {
                    transform: rotate(0deg);
                }
            }

            &:active + .menu__submenu,
            &:focus + .menu__submenu,
            &:active + .menu__submenu-menu,
            &:focus + .menu__submenu-menu {
                display: block;
            }

            */ }
      .menu__link--list::after {
        content: '';
        display: inline-block;
        width: 8px;
        height: 6px;
        margin-left: 10px;
        background-image: url("../img/icon-arrow.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        transform: rotate(180deg);
        transition: all 0.2s; }
    .menu__link--active::after {
      transform: rotate(0deg); }
    .menu__link--active + .menu__submenu {
      display: block; }
    .menu__link--active + .menu__submenu-menu {
      display: block; }

@media (min-width: 769px) {
  .menu {
    min-height: auto;
    max-width: 100%; }
    .menu__link {
      padding: 0;
      border-top: none; }
      .menu__link:hover {
        color: #EB5757; }
      .menu__link--list:hover {
        background-color: #131417; }
        .menu__link--list:hover::after {
          transform: rotate(0deg); }
      .menu__link--list:hover + .menu__submenu,
      .menu__link--list:hover + .menu__submenu-menu {
        display: block; }
      .menu__link span {
        max-width: 125px; }
    .menu__wrapper {
      display: flex;
      justify-content: space-between;
      width: 100%;
      max-width: 640px;
      margin: 0 auto;
      flex-wrap: wrap; }
    .menu__item {
      background-color: transparent;
      margin-right: 10px;
      position: relative; }
      .menu__item:last-of-type {
        margin-right: 0; }
      .menu__item:hover > .menu__link--list::after {
        transform: rotate(0deg); }
      .menu__item:hover .menu__submenu .menu__link--list:hover::after {
        transform: rotate(90deg); }
    .menu__submenu, .menu__submenu-menu {
      position: absolute; }
      .menu__submenu:hover, .menu__submenu-menu:hover {
        display: block; }
      .menu__submenu .menu__link span, .menu__submenu-menu .menu__link span {
        max-width: 100%; }
    .menu__submenu {
      top: 49px;
      border-radius: 0px 0px 8px 8px;
      padding-top: 35px;
      margin-top: -35px;
      position: absolute;
      max-width: 320px; }
      .menu__submenu::before {
        content: '';
        width: 20px;
        height: 20px;
        background-color: #252830;
        position: absolute;
        top: 25px;
        left: 50px;
        transform: rotate(45deg); }
      .menu__submenu .menu__link {
        background-color: #252830;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        padding: 10px 20px; }
        .menu__submenu .menu__link:hover {
          background-color: #131417; }
        .menu__submenu .menu__link--list:hover::after {
          transform: rotate(90deg); }
        .menu__submenu .menu__link--active::after {
          transform: rotate(90deg); }
      .menu__submenu .menu__item {
        margin-right: 0; }
        .menu__submenu .menu__item:first-of-type .menu__link {
          border-top: none; }
        .menu__submenu .menu__item:last-of-type .menu__link {
          border-radius: 0px 0px 8px 8px; }
        .menu__submenu .menu__item:hover > .menu__link--list::after {
          transform: rotate(90deg); }
    .menu__submenu-menu {
      position: absolute;
      right: -210px;
      bottom: 0;
      max-width: 215px;
      padding-left: 22px; }
      .menu__submenu-menu::before {
        content: '';
        width: 20px;
        height: 20px;
        background-color: #252830;
        position: absolute;
        bottom: 13px;
        left: 12px;
        transform: rotate(45deg); }
      .menu__submenu-menu .menu__item + .menu__item .menu__link {
        border-radius: 0px 0px 0px 0px; }
      .menu__submenu-menu .menu__item:first-of-type .menu__link {
        border-radius: 8px 8px 0px 0px; }
      .menu__submenu-menu .menu__item:last-of-type .menu__link {
        border-radius: 0px 0px 8px 8px; } }

.burger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  background-color: transparent;
  border: none;
  width: 22px;
  height: 12px;
  cursor: pointer;
  transition: all 0.2s;
  position: relative; }
  .burger span {
    display: inline-block;
    width: 100%;
    height: 2px;
    background-color: #F2994A;
    transition: all 0.2s; }
  .burger::before, .burger::after {
    content: '';
    display: inline-block;
    width: 100%;
    height: 2px;
    background-color: #F2994A;
    transition: all 0.2s; }
  .burger:hover span, .burger:hover::before, .burger:hover::after {
    background-color: #5BC12C; }
  .burger--opened {
    top: 5px; }
    .burger--opened::before, .burger--opened::after {
      position: absolute; }
    .burger--opened::before {
      transform: rotate(45deg); }
    .burger--opened::after {
      transform: rotate(-45deg); }
    .burger--opened span {
      opacity: 0; }

.header {
  background-color: #131417;
  padding: 17px 0;
  position: relative;
  z-index: 1; }
  .header__wrapper, .header__elements, .header__phone {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header__phone {
    margin-right: 30px; }
    .header__phone span {
      display: none; }
  .header__nav {
    position: absolute;
    top: 80px;
    right: 0; }
    .header__nav--hidden {
      right: -375px; }

@media (min-width: 376px) {
  .header__phone {
    margin: 0 auto; }
    .header__phone span {
      display: block;
      margin-left: 10px;
      font-weight: 500;
      font-size: 18px;
      line-height: 24px;
      transition: all 0.2s;
      color: inherit; }
    .header__phone:hover, .header__phone:focus {
      color: #F2994A; }
  .header__elements {
    flex-grow: 1; } }

@media (min-width: 769px) {
  .header__nav {
    padding: 0 20px;
    position: relative;
    top: 0; }
    .header__nav--hidden {
      right: 0; }
  .header__burger {
    display: none; }
  .header__elements {
    flex-grow: 0;
    min-width: 220px; } }
